.Profile-container {
  background-image: url('https://i.imgur.com/133AsXI.jpg');
  /* background-repeat: no-repeat; */
  background-size: cover;
  background-attachment: fixed;
}

.Profile-wrapper {
  /* background-color: #F0F0F0; */
  width: 80vw;
  margin: 0 auto;
}

.Profile-view {
  margin: 0 80px;
}

.Profile-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.Profile-header-top {
  display: -ms-flexbox;
  display: flex;
  margin-top: 80px;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
}

.Profile-header-bottom {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 50px;
  background-color: white;
  -webkit-box-shadow: 5px 6px 11px #8888885a;
          box-shadow: 5px 6px 11px #8888885a;
}

.Profile-tabs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 30%;
  position: absolute;
}

h1 {
  font-size: 28px;
  text-transform: capitalize;
}

h2 {
  line-height: 0;
}

.header-bar {
  display: -ms-flexbox;
  display: flex;
}

.back-btn {
  position: fixed;
  padding: 50px;
}

.Profile-headshots {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 90%;
  padding-bottom: 90px;
}

.curHeadshot {
  height: 150px;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 0 15%;
     object-position: 0 15%;
  width:150px;
  height:150px;
  border-radius: 100px;
  -webkit-box-shadow: 5px 6px 20px #8888885f;
          box-shadow: 5px 6px 20px #8888885f;
  z-index: 0;
  position: relative;
}

.Update-img-btn {
  position: absolute;
  z-index: 1;
  bottom: 26em;
}

@media only screen and (min-width: 1000px) {
  .Update-img-btn {
    position: absolute;
    z-index: 1;
    bottom: 35em;
  }
}

.initHeadshot {
  height: 150px;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 0 15%;
     object-position: 0 15%;
  width:150px;
  height:150px;
  border-radius: 100px;
}

.Profile-body {
  background-color: #D3D3D3;
  margin-top: 16px;
  padding: 20px;
  /* height: 80vh; */
}

.Sidebar {
  background-color: white;
  height: 200px;
}

.body-item {
  background-color: white;
}

.quote-icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 20%;
  margin: 0 auto;
}

.quote-text {
  font-size: 20px;
  text-align: center;
}

.strong {
  font-weight: 600;
}

.Update-btn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.highlight {
  /* font-family: 'Raleway', sans-serif; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.highlight li {
  font-family: 'Raleway', sans-serif;
}

h4 {
  margin: 0;
}

p.highlight, ul {
  padding-left: 10px;
}

i.highlight {
  margin: 10px;
}

/* ------ Mobile Styling ------ */

@media only screen and (max-width: 1000px) {
  .Profile-body {
    margin: 0 auto;
    margin-top: 0;
    padding: 8px;
  }
  
  .Profile-wrapper {
    width: 100vw;
  }

  .Profile-container {
    margin: 0 5px;
  }
  
  .Profile-view {
    margin: 0 10px;
  }
  
  .Profile-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
  }
  
  .Profile-header-top {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 100px;
  }
  
  .Profile-header-bottom {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
    height: 100%;
  }
  
  .Profile-tabs {
    width: 100%;
    margin: 15px;
    padding: 0 20px;
    position: inherit;
  }

  .Sidebar {
    display: none;
  }
  
  .back-btn {
    padding: 20px;
  }
  
  .Profile-headshots {
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: inherit;
    padding-bottom: 0;
    margin: 15px;
  }
  
  .headshot {
    height: 130px;
  }
}


